body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
.Main {
  border-width: 1pt;
  border-style: solid;
  margin: 11px 0%;
  margin-right: 5%;
  padding-top: 15px;
  border-radius: 70px;
  border-color: purple;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.flex {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.half {
  width: 40%;
  margin: 0 5%;
}
.space {
  margin-left: 2%;
}
.button {
  background-color: cornsilk;
  padding: 0 2%;
}
.label {
  text-align: left;
  font-weight: bolder;
}
.otherDataBtn {
  margin-right: 10%;
}

.greenBorder{
  border-color: green;
}

.redBorder{
  border-color: red;
}
.userListItem {
  
  border-width: 2px;
  border-style: solid;
  text-align: left;
  margin: 20px auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 5px 35px;
}

.userActivityDiv {
  text-align: left;
  border-color: black;
  border-width: 2px;
  border-style: solid;
  padding: 10px;
  margin: 10px 10px 20px 0;
  max-height: 350px;
  overflow: scroll;
}
.left {
  text-align: left;
}
.floatrightBtn {
  float: right;
  margin-right: 10px;
}
.topdiv {
  margin-top: 20px;
}

.posttodoItem {
  border-style: solid;
  border-color: black;
  border-width: 1px;
  padding: 10px;
  margin: 20px 0;
}
.hideBtn {
  display: none;
}
.todoBtn {
  float: right;
  position: relative;
  bottom: 22px;
}
.rowDiv {
  margin-bottom: 5px;
}
.otherDetails {
  background-color: silver;
  border-style: solid;
  border-color: black;
  border-width: 1px;
  border-radius: 30px;
  margin: 10px 0;
  padding: 10px;
}
.margleft{
  margin-left: 7%;
}
.addpost{
  border-color: black;
  border-width: 2px;
  border-style: solid;
  padding-top: 50px;
  margin: 10px 10px 20px 0;
  height: 100px;
}
.addcancelbtns{
  direction: rtl;
  margin-top: 25px;
  margin-right: 25px;
}
.addcancelbtns input{
  margin:0 10px;
  padding: 0 10px;
}
.selectedUser{
  background-color: orange;
}
.uncompletedTasks{
  border-color:red;
}

.completedTasks{
  border-color:green;
}
a{
  text-decoration: none;
}
